.container {
    /* background-color: #fff; */
    width: 800px;
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    -ms-flex-direction: column;
        flex-direction: column;
    border: 7px solid #FEFCFF;
    margin-top: 20px;
    -webkit-box-sizing: border-box;
