}

.albums__albums {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /* margin: auto; */
    margin-left: 10px;
    margin-top: 13px;
}

.albums__concerts {
  display: none;
  /* visibility: hidden; */
  -ms-flex-direction: column;
      flex-direction: column;
  margin: auto;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0;
  margin-left: -10px;
}

.albums__cont {
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.title {
    text-align: center;
    margin-top: 10px;
}

.button__albums {
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    background-color: #FEFCFF;
}

.button__albums:active,
.button__albums:focus {
    border-color: rgb(216, 181, 221);
    -webkit-box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
            box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
    background-color: #FEFCFF;
}

.spin {
    margin-top: 30px;
    margin-bottom: 40px;
}

.errorText {
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 27px;
}

.subElementCont {
  width: 30px;
  height: 30px;
}

.subscribeElement {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color:pink;
  height: 35px;
  width: 35px;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.subscribeElement:after {
  background-color: pink;
  content: "";
  border-radius: 50%;
  position: absolute;
  height: 35px;
  width: 35px;
  top: 0px;
  left: 25px;
}
.subscribeElement:before {
  content: "";
  background-color: pink;
  border-radius: 50%;
  position: absolute;
  height: 35px;
  width: 35px;
  top: -25px;
  left: 0px;
}

.heart {
  background-color: #cb9dd2;
  display: inline-block;
  height: 30px;
  margin: 0 10px;
  position: relative;
  top: 11px;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  width: 30px;
  left: 0px;
}

.heart:hover {
  cursor: pointer;

}

.heart::before,
.heart::after {
  content: "";
  background-color: #cb9dd2;
  border-radius: 50%;
  height: 30px;
  position: absolute;
  width: 30px;
}

.heart:before {
  top: -15px;
  left: 0;
}

.heart:after {
  left: 15px;
  top: 0;
}

.heart--inSubs {
  background-color: #f2cdf8;
}
.heart--inSubs::after,
.heart--inSubs:before {
  background-color: #f2cdf8;
}
.heart--inSubs{
  cursor: default;
}

.button__albums--back {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 220px;
  height: auto;
  max-width: 220px;
  background-color: #fff;
  margin-top: -96px;
  position: absolute;
  z-index: 23;
  right: 0;
}

.button__albums--back:focus {
  outline: 0;
}

.toolbar__albums--AlConHeart {
  padding-top: 10px;
  height: 65px;
}

.mySubs--albums { 
  margin-top: -96px;
}


