.form {
    width: 780px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 10px;
}

.form__input {
    -ms-flex-line-pack: center;
        align-content: center;
    /* background-color: azure; */
    width: 780px;
    display: -ms-flexbox;
    display: flex;
}

.form__search-label-col,
.form__search-label,
.form__button-col,
.form__button {
    display: -ms-flexbox;
    display: flex;
    margin: auto;
}

.form__form-control:focus,
.form__button:focus {
    border-color: #E9CFEC;
    -webkit-box-shadow: 0 0 0 0.2rem rgba(233,207,236,.5);
            box-shadow: 0 0 0 0.2rem rgba(233,207,236,.5);
}

.form__form-control:active,
.form__button:active {
    border-color: rgb(216, 181, 221);
    -webkit-box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
            box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
}

.form__button {
    background-color: #F5E3FA;
}

.form__button:active {
    background-color: #F3D9F6;
}


